* {
  box-sizing: border-box;
}

body {
  font-size: 13px; /*increased the fontsize to more than 12px for SEO*/
  font-family: "Helvetica Neue", Helvetica,sans-serif, Tahoma, Arial;
  height: 500px;
}

svg {
width: 100%;
  height: 100%;
  text-align: center;
}

path.slice{
stroke-width:2px;
}

polyline{
opacity: .3;
stroke: black;
stroke-width: 2px;
fill: none;
}



header{
  height: 400px;
  background-color: #b4c6f0;
  background-image: url('./assets/bg.png');
  background-size: 400px;
  background-repeat: no-repeat;
  background-position: center bottom;
  padding-top: 40px;
}

header h1,
header h2 {
  text-align: center;
  margin: 0;
}

header h1 {
  color: #fff;
  font-size: 4.2em;
  margin-bottom: 20px;
}

header h2 {
  color: #4d5791;
}

header h3 {
  text-align: right;
  margin-right: 100px ;
}

.skip {
  background: #319795;
  color:#fff;
  font-weight: 700;
  left: 50%;
  padding: 4px;
  position: absolute;
  transform: translateY(-100%);
}

.skip:focus {
  transform: translateY(0%);
}

.skip{
  transition: transform 0.3s;
}

.skip:focus-within {
  transform: translateY(0%);
}

main {
  padding: 40px;
  width: 100%;
}

main article {
  display: inline-block;
  width: 100%;
  max-width: 340px;
  text-align: justify;
  margin: 0;
}

footer {
  height: 50px;
  background-color: #b4c6f0;
  padding: 40px;
}

.center {
  text-align: center;
}

_inline-block {
  display: inline-block;
  width: 100%;
  max-width: 340px;
  text-align: justify;
  margin: 20px;
}

@media screen and (min-width: 768px) {
  .container .div.text-box {
      margin: 10px;
  }
}
